
import Head from 'next/head'
import {Container} from 'semantic-ui-react'
import Header from './Header'
export default ({children, title = ''}) => (
  <React.Fragment>
    <Head>
    <meta name="viewport" content="width=device-width, initial-scale=1" />
          <meta charSet="utf-8" />
          <link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.2.2/semantic.min.css"/>
          <title>{title}</title>
    </Head>

    <Header />
     <style jsx global>{`
      body { 
        background-image: url('https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/GfRLKaE/videoblocks-4k-night-sky-clouds-stars-galaxy-universe-background_bj7v4h7iz_thumbnail-full01.png');
        color: white;
      }
    `}</style>

    <Container text style ={{paddingTop: '7em'}}>
      {children}
    </Container>
  </React.Fragment>
)


